import Cross from "../images/cross.png"

function Popup ({popupStates, onCrossClick, onRulesClick}) {

    return (
        <div className={"popup " + (popupStates.hidePopup ? "hidden" : "")}>
            <div className="info-container">
                <img src={Cross} onClick={onCrossClick}/>
                <p>{popupStates.popupMessage}</p>
                <button className={popupStates.hideButton ? "hidden" : ""} onClick={onRulesClick}>{popupStates.buttonMessage}</button>
            </div>
        </div>
    );
};

export default Popup;