const rules = `INTRODUCTION
Dans la forêt de Sherwood, la lutte entre les compagnons de Robin des Bois et les adjoints du shérif de Nottingham fait rage. Chaque camp cherche à infiltrer le clan adverse pour enfin dominer la région. En tant que simple citoyen, vous êtes coincé entre ces deux factions. Vous sauverez votre peau en dénonçant Robin et ses compagnons, ou en démasquant le shérif et ses adjoints, infiltrés dans le campement de Robin.
\nBUT DU JEU
Le premier joueur à démasquer les 3 cartes d’un camp (Le Sheriff et ses adjoint ou Robin et ses compagnons) remporte la partie. Un joueur remporte aussi la partie si l’autre joueur n’a plus de vies. Chaque joueur commence avec 2 vies.
\nDÉROULEMENT
Voici comment se déroule le tour d’un joueur :
•	Vous retournez une carte face cachée sans que l’autre joueur ne la voit puis vous la reposez. Attention : Si vous pioché le traitre, vous perdez une vie et votre tour s’arrête.
•	Vous prenez ensuite la décision de passer votre tour, d’annoncer le personnage que vous venez de consulter ou bien d’en annoncer un autre et dans ce cas, vous êtes un vilain menteur. Attention : Si vous avez pioché l’embobineur, vous êtes obligé de mentir !
•	Le joueur adverse peut choisir de vous croire ou vous accuser de mentir.
•	S’il ne vous croit pas et que vous mentez, vous perdez une vie, autrement c’est lui qui perd une vie et vous utilisez votre pouvoir.
•	S’il vous croit, vous utilisez le pouvoir du personnage que vous avez annoncé :
o Robin : Le pouvoir de Robin est de sélectionner 3 cartes face cachées. Si derrière ces cartes se cachent le Sherif et ses 2 adjoints, vous remportez la partie, autrement vous perdez une vie. Lorsque vous êtes Robin, vous pouvez aussi choisir de ne pas démasquer et de passer votre tour.
o Compagnon : Lorsque vous déclenchez le pouvoir du compagnon, vous rejouez.
o Sheriff : Le pouvoir du Sheriff est de sélectionner 3 cartes face cachées. Si derrière ces cartes se cachent Robin et ses 2 compagnons, vous remportez la partie, autrement vous perdez une vie. Lorsque vous êtes le Sheriff, vous pouvez aussi choisir de ne pas démasquer et de passer votre tour.
o Adjoint : Lorsque vous déclenchez le pouvoir de l’adjoint, vous sélectionnez deux cartes face cachée qui échangent de place.
o Paysanne : Il ne se passe rien.
o Moine : Vous pouvez choisir une carte face cachée qui sera visible jusqu’à la fin de la partie.
o Sorcière : Vous pouvez consulter une carte du plateau sans que l’autre joueur ne la voit.
•	Une fois que vous avez déclenché un pouvoir, c’est au tour de l’autre de joueur.`

export default rules;